<template>
  <div>
    <div id="header">
        <h4></h4>
    </div>
    <div ref="myscroller" class="scrobox">
      <div class="movielist" v-for="(item,index) in movie.subjects" :key="index">
        <a>
          <img :src="item.images.small" alt="">
        </a>
        <span>{{item.title}}</span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        movie: {},

      };
    },
    created(){
      this.getNewmovie()
    },
    methods: {
        getNewmovie(){
        const that = this;
        $.ajax({
          url: "https://api.douban.com/v2/movie/in_theaters",
          dataType: "jsonp",
          success: function(backdata) {
            that.movie = backdata;
            console.log("w");
            
            console.log(backdata);
            
          }
        })
      },
    }
  }
</script>

<style lang="less">

    #header {
      width: 100%;
      height: 40px;
      font-size: 14px;
      background-color: #ff0036;
      color: #fff;
      text-align: center;
      >h4 {
          font-weight: normal;
          line-height: 40px;
      }
    }
    .scrobox {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      .movielist {
        font-size: 12px;
        width: 100%/3;
        height: 175px;
        padding: 5px;
        > a {
          display: block;
          width: 100%;
          height: 135px;
          margin-bottom: 8px;
          overflow: hidden;
          > img {
            width: 100%;
          }
        }
        > span {
          display: block;
          width: 100%;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
</style>



