<template>
  <div>
    <header id="header">
        <div class="top">
            <h1 class="logo"></h1>
            <!-- <router-link to="/seach"> -->
                <router-link to="/seach" class="seach">
                    <span class="glyphicon glyphicon-search"></span>
                    <span class="">   </span>
                </router-link>
            <!-- </router-link> -->
            <a class="donwload glyphicon glyphicon-save" href=""></a>
        </div>

        <!--  -->
        <nav id="nav">
            <ul>
                <li class="active"></li>
                <li></li>
                <li></li>
                <li></li>
            </ul>
        </nav>
    </header>

    <div ref="myscroller" class="scrobox">
      <div class="content">
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div class="swiper-slide">
              <a href="">
                <img src="../../static/images/banner-01.jpg" alt="">
              </a>
            </div>
            <div class="swiper-slide">
              <a href="">
                <img src="../../static/images/banner-02.jpg" alt="">
              </a>
            </div>
            <div class="swiper-slide">
              <a href="">
                <img src="../../static/images/banner-03.jpg" alt="">
              </a>
            </div>
            <div class="swiper-slide">
              <a href="">
                <img src="../../static/images/banner-04.jpg" alt="">
              </a>
            </div>

          </div>
          <!-- Add Pagination -->
          <div class="swiper-pagination"></div>
        </div>

        <div class="movielist" v-for="(item,index) in movie.subjects" :key="index">
          <router-link to="/moviedata">
            <img :src="item.images.small" alt="">
          </router-link>
          <span>{{item.title}}</span>
          <p @click="addMovie(item)"><i class="glyphicon glyphicon-ok-circle"></i></p>
        </div>

        <div class="movielist" v-for="item in moreMovie" :key="item.id">
          <router-link to="/moviedata">
            <img :src="item.subject.images.small" alt="">
          </router-link>
          <span>{{item.title}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll"
  export default {
    data() {
      return {
        movie: {},
        moreMovie: {}
      };
    },
    created() {
      this.getNewmovie()
      this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.myscroller,{
        click: true
      });
    });
    },
    mounted() {
      var swiper = new this.Swiper(".swiper-container", {
        pagination: {
          el: ".swiper-pagination"
        }
      });
    },
    
    methods: {
      // 
      getNewmovie(){
        const that = this;
        $.ajax({
          url: "https://api.douban.com/v2/movie/in_theaters",
          dataType: "jsonp",
          success: function(backdata) {
            that.movie = backdata;
            // console.log(that.movie)
          }
        })
      },

      // 
      getYumovie(){
        const that = this;
        $.ajax({
          url: "https://api.douban.com/v2/movie/coming_soon",
          dataType: "jsonp",
          success: function(backdata) {
            that.movie = backdata;
            // console.log(that.movie)
            that.$refs.myscroller.finishPullToRefresh()
          }
        })
      },

      // 
      getMoreMovie(){
        const that = this;
        $.ajax({
          url: "https://api.douban.com/v2/movie/us_box",
          dataType: "jsonp",
          success: function(backdata) {
            that.moreMovie = backdata.subjects; 
            that.$refs.myscroller.finishInfinite(true)  
            
          }
        })
      },

      // 
      addMovie(item){
        const movieLocal = {
          id: item.id,
          title: item.title,
          images: item.images.small
        }
        console.log("daole");
        
        this.$store.commit("addMovie",movieLocal)
        

      }
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

  #header {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 10;
      width: 100%;
      height: 90px;
      background-color: white;

      .top {
          width: 100%;
          height: 45px;
          display: flex;
          justify-content: center;
          align-items: center;
          
          .logo {
              flex-basis: 30px;
              height: 30px;
              margin: 0px 15px;
              background: url("../../static/images/logo.png") no-repeat center;
              background-size: 100%;
          }
          .seach {
              height: 30px;
              line-height: 30px;
              text-align: center;
              flex: 1;
              color: #666;
              background-color: #ededed;
              font-size: 12px;
          }
          .donwload {
              height: 30px;
              margin: 0px 15px;
              font-size: 20px;
              line-height: 30px;
              color: #ff0036;

              
          }
      }

      #nav {
          width: 100%;
          height: 45px;
          color: #666;

          .active {
              border-bottom: 2px solid #ff0036;
          }
          // background-color: red;
          >ul {
              width: 100%;
              height: 100%;
              display: flex;
              justify-content: center;
              align-items: center;
              >li {
                  flex: 1;
                  height: 100%;
                  line-height: 45px;               
                  text-align: center;

              }
          }
      }
  }

  .scrobox {
    height: 100%;
    position: relative;
    top: 0px;
    left: 0px;
    overflow: hidden;
    

    .content {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      bottom: 0px;
      padding-top: 90px;

      .swiper-container {
        width: 100%;
        .swiper-slide {
          width: 100%;
          a {
            display: block;
            width: 100%;
            text-align: center;
            overflow: hidden;

            img {
              width: 150%;
              margin-left: -25%;
            }
          }
        }
      }

      > .movielist {
        font-size: 12px;
        width: 100%/3;
        height: 185px;
        padding: 5px;
        > a {
          display: block;
          width: 100%;
          height: 135px;
          margin-bottom: 8px;
          overflow: hidden;
          > img {
            width: 100%;
          }
        }
        > span {
          display: block;
          width: 100%;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
  }

</style>



