<template>
  <div class="seachbox">
    <div id="header">
        <h4></h4>
        <router-link to="/" class="glyphicon glyphicon-menu-left"></router-link>
    </div>

    <div id="main">
        <!--return false  -->
        <form onsubmit="return false">
            <input type="text" placeholder="" v-model="start">
            <button class="butt" @click="seachMovie"> </button>
            <!-- <router-link class="butt" to="/movielist/11"> </router-link> -->
        </form>
        <div class="his">
            <span class="pull-left"></span>
            <span class="delete pull-right" @click="deleteAllHistory">
                <i class="glyphicon glyphicon-trash "></i>
                <i></i>
            </span>
        </div>

        <ul class="history" @click="historydothing">
            <!-- <li v-for="item in historydata" :key="item.id"><span @click="seachHistoryMovie(item.content)">{{item.content}}</span><span @click="deleteHistroy(item.id)" class="glyphicon glyphicon-remove-circle pull-right"></span></li> -->
            <li v-for="item in historydata" :key="item.id"><span class="content">{{item.content}}</span><i :data-id="item.id" class="glyphicon glyphicon-remove-circle pull-right"></i></li>
        </ul>
    </div>
    <div class="tan" v-show="showtan">
         , !
    </div>
  </div>
</template>

<script>
    export default {
        data(){
            return {
                start: "",
                historydata:{},
                showtan:false
            }
        },
        created(){
            this.findHistory()
        },
        methods: {
            
            // 
            seachMovie(){
                // this.$router.push({ name: 'movielist', params: { userId: 123}})
                if(this.start==""){
                    this.showtan=true;
                    setTimeout(()=>{
                       this.showtan=false; 
                    },800)
                    return;
                }
                this.$router.push({path: `/movielist/${this.start}`})
                this.seachHistory()
            },

            // 
            historydothing(e){
                const tent = e.path[0].localName
                if(tent=="span"){
                    this.start = e.path[0].innerText
                    this.$router.push({path: `/movielist/${this.start}`})  
                }else if(tent=="i"){
                    const deleteId = e.target.dataset.id
                    this.deleteHistroy(deleteId)    
                }
            },

            // 
            seachHistory(){
                
                    var content = this.start;

                    // console.log(content);
                    var history = localStorage.getItem("history");
                    
                    var arry;
                    if(history){
                        arry = JSON.parse(history);
                    }else{
                        arry = []; 
                    }
                    var id;
                    if(arry.length>0){
                        id= arry[arry.length-1].id+1;
                    }else {
                        id = 1;
                    }
                    var obj = {
                        id: id,
                        content: content,
                    }
                    arry.push(obj);

                    localStorage.setItem("history",JSON.stringify(arry));

                    // this.start=""        
            },
            

            // 
            findHistory(){
                var history = localStorage.getItem("history");
                
                var arry;
                if(history){
                    arry = (JSON.parse(history)).reverse();
                }else{
                    arry = []; 
                }
                this.historydata = arry  
            },

            // 
            deleteAllHistory(){
                
                localStorage.removeItem("history")
                this.historydata={}          
            },

            // 
            deleteHistroy(id){
                var history = localStorage.getItem("history");
                var arry;
                if(history){
                    arry = (JSON.parse(history)).reverse();
                }else{
                    arry = []; 
                }
                for(var i=0;i<arry.length; i++){
                    if(arry[i].id == id){
                        arry.splice(i,1);
                        break;
                    }
                }
                arry = JSON.stringify(arry);
                localStorage.setItem("history",arry);
                this.findHistory();    
            }
        }   
    }  
</script>

<style scoped lang="less">
    .seachbox {
        width: 100%;
        height: 100%;
        background-color: white;
        position: absolute;
        z-index: 1000;
        top: 0px;
    }

    #header {
        width: 100%;
        height: 40px;
        font-size: 14px;
        background-color: #ff0036;
        color: #fff;
        text-align: center;
        >h4 {
            font-weight: normal;
            line-height: 40px;
        }
        .fa-search {
            position: absolute;
            right: 20px;
            top: 15px;
            font-size: 20px;
            color: #fff;

        }
        .fa-home{
            position: absolute;
            right: 20px;
            top: 15px;
            font-size: 25px;
            color: #fff;
        }
        .glyphicon-menu-left {
            position: absolute;
            left: 15px;
            top: 10px;
            font-size: 20px;
            color: #fff;

        }
    }

    #main {
        padding: 20px 15px 20px 15px;
        >form {
            position: relative;
            width: 100%;
            border-radius: 5px;
            border: 1px solid red;
            height: 35px;
            margin-bottom: 15px;
            overflow: hidden;

            >input {
                width: 80%;
                height: 100%;
                font-size: 12px;
                text-indent: 8px;
            }

            >.butt {
                border: none;       
                border-radius: 0px;
                width: 20%;
                height: 100%;
                text-align: center;
                line-height: 35px;
                font-size: 15px;
                color: #fff;
                position: absolute;
                right: 0;
                background-color: #ff0036;
                
            }

            
        }

        >.his {
            height: 40px;
            span {
                font-size: 14px;
                color: #666;
                line-height: 40px;
            }

        }
        .history {
            font-size: 14px;
            color: #666;

            >li {
                margin-top:-1px;
                width: 100%;
                padding: 10px 10px;
                line-height: 100%;
                border: 1px solid #ddd;

            }
            

        }
    }

    .tan {
        position: fixed;
        left: 50%;
        transform: translate(-50%);
        bottom: 50px;
        color: #ff0036;
    }
</style>




