const KEY = "movieInfo"

// 
// :{ movieInfo: [ {id:***,title:***,image:***.jpg} ] } 
const getLocalMovie = ()=>{
    return JSON.parse(localStorage.getItem(KEY) || '{"movieInfo":[]}')
}

// 
const getLocalMovieCount = ()=> {
    const LocalMovie = getLocalMovie()

    let MovieCount = LocalMovie.movieInfo.length

    return MovieCount

}

//

// movies {id:***,title:***,image:***.jpg}
const addLocalMovie = (movies)=> {



    const LocalMovie = getLocalMovie().movieInfo
    console.log(LocalMovie);
    
    const movieId = movies.id
    const movieLength = LocalMovie.length
    var flag = true
    for(var i = 0; i<movieLength; i++) {
        if(movieId==LocalMovie[i].id){
            flag = false
            break
        }
    }

    if(flag==true){
        LocalMovie[LocalMovie.length] = movies
        const LocalMovieInfo = {movieInfo:LocalMovie}
        console.log(LocalMovieInfo);
        
        localStorage.setItem(KEY,JSON.stringify(LocalMovieInfo))
    }

    return getLocalMovieCount()

}

export {getLocalMovie,getLocalMovieCount,addLocalMovie}


