<template>
  <div>
      <header id="header">
          <div class="top">
              <h1 class="logo"></h1>
              <!-- <router-link to="/seach"> -->
                  <router-link to="/seach" class="seach">
                      <span class="glyphicon glyphicon-search"></span>
                      <span class="">   </span>
                  </router-link>
              <!-- </router-link> -->
              <a class="donwload glyphicon glyphicon-save" href=""></a>
          </div>
      </header>

      <div class="main" ref="wrapper" >
          <div class="scollbox">
            <div v-if="isMovieData" class="movielist" v-for="(item,index) in movieList" :key="index">
              <a href="">
                <img :src="item.images.small" alt="">
              </a>
              <span>{{item.title}}</span>
            </div>
          </div>

          <div v-if="!isMovieData" class="span">
            <div class="eye"></div>
          </div>
      </div>
    </div>
</template>

<script>
import BScroll from "better-scroll"
export default {
  data() {
    return {
      star: this.$route.params.keyword,
      movieList: {},
      isMovieData: false
    };
  },
  beforeCreate() {},
  created() {
    // console.log(this.star);
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper);
    });
  },
  mounted() {
    this.getMovieList();
  },
  methods: {
    getMovieList() {
      const that = this;
      $.ajax({
        url: `https://api.douban.com/v2/movie/search?q=${this.star}`,
        dataType: "jsonp",
        success: function(backdata) {
          that.movieList = backdata.subjects;
          that.isMovieData = true;
        }
      });
    }
  }
};
</script>

<style  scoped lang="less">

#header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 45px;
    background-color: white;

    .top {
        width: 100%;
        height: 45px;
        display: flex;
        justify-content: center;
        align-items: center;
        
        .logo {
            flex-basis: 30px;
            height: 30px;
            margin: 0px 15px;
            background: url("../../static/images/logo.png") no-repeat center;
            background-size: 100%;
        }
        .seach {
            height: 30px;
            line-height: 30px;
            text-align: center;
            flex: 1;
            color: #666;
            background-color: #ededed;
            font-size: 12px;
        }
        .donwload {
            height: 30px;
            margin: 0px 15px;
            font-size: 20px;
            line-height: 30px;
            color: #ff0036;

            
        }
    }

}

.main {
  width: 100%;
  height: 100%;
  overflow: hidden;

  .scollbox {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      padding-top: 45px;

    .movielist {
      font-size: 12px;
      width: 100%/3;
      height: 230px;
      padding: 5px;
      > a {
        display: block;
        width: 100%;
        height: 200px;
        margin-bottom: 2px;
        overflow: hidden;
        > img {
          width: 100%;
        }
      }
      > span {
        display: block;
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }

  }

}
.span {
  width: 50px;
  height: 20px;
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.eye {
  width: 20px;
  height: 20px;
  background-color: #ff0036;
  border-radius: 50%;
  box-shadow: 30px 0px 0px 0px #ff0036;
  position: relative;
}

.eye:after {
  background-color: #fff;
  width: 8px;
  height: 8px;
  box-shadow: 30px 0px 0px 0px #fff;
  border-radius: 50%;
  left: 9px;
  top: 10px;
  position: absolute;
  content: "";
  animation: eyeball 1s linear infinite alternate;
}
@keyframes eyeball {
  0% {
    left: 9px;
  }
  100% {
    left: 1px;
  }
}
</style>




